* {
  padding: 0;
  margin: 0;
}
.banner {
  background-image: url("https://wallpaperaccess.com/full/2703652.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  color: white;
  background-position: center;
  height: auto;
  padding-top: 25vh;
}

.content {
  height: auto;
  width: 100vw;
  padding-left: 15px;
  padding-top: 5vh;
}

.title {
  font-size: 3.5vw;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.button {
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 3vw;
  padding-right: 3vw;
  padding-top: 1vw;
  padding-bottom: 1vw;
  background-color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  margin-right: 2vw;
  font-size: 2vw;
}

.button:hover {
  color: black;
  background-color: #e6e6e6;
}

.description {
  width: 95vw;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 2vw;
  max-width: 50%;
}

.fade_bottom {
  height: 11.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
