.body {
  max-width: 100vw;
}

.navbar {
  position: fixed;
  color: #e6e6e6;
  top: 0;
  width: 100%;
  height: 90px;
  /* padding: 20px; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #111;
  z-index: 20;
}

.logo {
  position: fixed;
  left: 20px;
  width: 80px;
  z-index: 100;
}

.avatar {
  width: 30px;
}

.Navigation .navlinks {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 80vw;
  font-size: 1rem + 1vw;
  font-weight: 500;
}

.navList p:hover,
.avatar:hover,
.logo:hover {
  cursor: pointer;
  color: rgb(134, 134, 134);
}

.mobileNavigation .navlinks {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 300px;
  margin-top: 300px;
  opacity: 0.95;
  width: 110vw;
  height: 80vh;
  background-color: #181818;
}

@media (max-width: 900px) {
  .Navigation {
    display: none;
  }
  .mobileNavigation {
    display: flex;
  }
  .hamburger {
    display: block;
  }
}

@media (min-width: 900px) {
  .Navigation {
    display: block;
  }
  .mobileNavigation {
    display: none;
  }
  .hamburger {
    display: none;
  }
}

.hamburger {
  z-index: 100;
  position: fixed;
  right: 10vw;
}
